body {
   font-family: 'Rubik', sans-serif;
}

.wrapper {
   min-width: 100%;
   margin: 0;
   list-style: none;
}


.page__background {
   position: absolute;
   z-index: 1;

}
.page__cover img{
   max-width: 100%;
   max-height: 100%;
}


.page__map {
}


.map__flag {
}

.page__container {
   position: relative;
   max-width: 1000px;
   margin: 0 auto;
   padding: 0 15px;
   z-index: 1000;
   box-sizing: content-box;
   display: flex;
   flex-direction: column;
   align-items: center;
}
.page__content {
   margin: 80px 0 0 0;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.content__title {
   color: aliceblue;
}
.content__input {
   margin: 20px 0px 20px 0px;
   box-shadow: 3px 6px 24px -18px rgba(54, 54, 64, 0.75);
   input {
      min-width: 500px;
      font-family: 'Rubik', sans-serif;
      padding: 20px;
      font-size: 16px;
      border-radius: 6px 0 0 6px;
      border: none;

      &::placeholder {
     
         font-size: 16px;
      }

      
   }
   textarea:focus, input:focus{
      outline: none;
  }
      .input button {
         min-height: 100%;
         background-color: #000000cb;
         border: none;
         padding: 21px;
         border-radius: 0 6px 6px 0;
         margin-left: -5px;
         transition: 0.25s background-color ease-out;
         

         &:hover {
            background-color: #525561;
         }
   }


}
.content__info {
   background-color: #fff;
   border-radius: 6px;
   padding: 30px;
   box-shadow: 3px 6px 24px -18px rgba(54, 54, 64, 0.75);

   .info__body {
      display: flex;
      flex-direction: row;
   }
   .info__item {
      min-width: 214px;
   }
   .info__item:not(:last-child) {
      border-right: 1px solid rgba(102, 102, 102, 0.315);
   }
   
   .item__title  {
      margin-bottom: 10px;
      padding: 0px 20px 0px 20px;
    

      p {
      font-weight: 700;
      color: #666666bd;
      font-size: 13px;
      }
   
   }

   .item__text {
      color: #000000cb;
      max-width: 164px;
      font-weight: 700;
      font-size: 20px;
      padding: 0px 30px 0px 20px;
   }
}